<template>
    <div>
      <p>我是ajax的使用方法</p>
      <p> </p>
    </div>
</template>

<script>
    import {getClassList} from "api/test/ajax";
    export default {
      name: "index",
      methods:{
        getDataList(){
          getClassList({
            data:{},
            success:res=>{
              console.log("请求结果",res);
              alert(res.message)
            },
            error:err=>{
              console.log("err",err);
            }
          })
        }
      },
      mounted(){
        this.getDataList()
      }

    }
</script>

<style scoped>

</style>



